import React, { Component } from 'react';

class CallToAction extends Component {
  render() {
    if(this.props.data){
      var heading = this.props.data.ctatitle;
      var content = this.props.data.ctacontent;
      var link = this.props.data.ctalink;
  
    }
    return (
        <section id="call-to-action">

        <div className="row">
     
           <div className="two columns header-col">
     
              <h1><span>Get Hosting.</span></h1>
     
           </div>
     
           <div className="seven columns">
     
              <h2><a href="http://www.dreamhost.com/r.cgi?287326|STYLESHOUT">{heading}</a></h2>
              <p>{content}</p>
     
           </div>
     
           <div className="three columns action">
     
              <a href={link} className="button">Sign Up Now</a>
     
           </div>
     
        </div>
     
     </section>
    );
  }
}

export default CallToAction;



